.j-item-options li,
.j-item-options {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}
.j-item-options li:hover,
.j-item-options li:focus {
  border-color: var(--input-border);
  background: var(--light-bg-color--hover);
  outline: 0;
}
.j-item-options li.active {
  cursor: text;
  background-color: var(--background);
  border-color: var(--input-border);
  box-shadow: inset 999rem 0 var(--background);
  -webkit-touch-callout: text;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
  -moz-transition: 0.5s;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.j-item-options li.active .icon {
  opacity: 1;
}
.j-item-options li {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 3px;
  padding: 10px 10px 10px 68px;
  min-height: 68px;
  margin: 2px 0;
}
.j-item-options li label {
  display: block;
  font-size: var(--font-size-base);
  font-weight: bold;
  color: var(--text-color);
  padding-bottom: 5px;
  cursor: pointer;
}
.j-item-options li label input {
  position: absolute;
  top: 24px;
  left: 25px;
  display: none;
}
